.markdown-text p,
.preview p {
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  word-wrap: break-word;
  line-height: 2em;
  margin: 1em 0;
}
.markdown-text > :last-child,
.preview > :last-child,
textarea#title {
  margin-bottom: 1em;
}
.markdown-text > :first-child,
.preview > :first-child {
  margin-top: 0;
}
.markdown-text li,
.preview li {
  font-size: 14px;
  line-height: 2em;
}
.markdown-text li code,
.markdown-text p code,
.preview li code,
.preview p code {
  color: #000;
  background-color: #fcfafa;
  padding: 4px 6px;
}
.markdown-text img {
  cursor: pointer;
}
.markdown-text h1 code,
.markdown-text h2 code,
.markdown-text h3 code,
.markdown-text h4 code,
.markdown-text h5 code,
.markdown-text h6 code {
  font-size: inherit;
  color: inherit;
}
.panel .markdown-text a {
  color: #08c;
}
.preview {
  padding: 0.5em;
  font-size: 15px;
  min-height: 200px;
  word-break: break-all;
}
.preview p > img {
  display: block;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.6);
}
.markdown_editor .markdown_in_editor,
.markdown_editor .markdown_in_preview {
  display: none;
}
.markdown_editor.in_editor .markdown_in_editor,
.markdown_editor.in_preview .markdown_in_preview {
  display: block;
}



