
.topic {
  word-break: break-word;
}
.markdown-body {
  border-bottom: 1px solid #e0e0e0;
}
.topic-title {
  margin-bottom: 20px;
  padding: 5px;
  font-size: 18px;
  background: #f0f0f0;
  border-radius: 6px;
}
.topic-msg {
  margin-bottom: 30px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  font-size: 13px;
  line-height: 20px;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.topic-msg .left {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.topic-msg .left img {
  margin-right: 5px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.topic-msg .right .tag {
  background: #4badda;
  color: #fff;
  text-align: center;
  border-radius: 5px;
}
.reply_count {
  padding: 15px 0;
  border-bottom: 1px solid #e0e0e0;
}
.reply_count b {
  color: #4bda89;
}



